:root {
  --text: #05100f;
  --background: #e8f8f7;
  --background-100: #d8f3f1;
  --background-200: #b1e7e3;
  --background-300: #8adbd6;
  --background-400: #63cfc8;
  --background-500: #3cc3ba;
  --primary: #306a9c;
  --secondary: #b9bae9;
  --secondaryHover: #8b8cda;
  --accent: #3236a4;
  --highPriority: #d00000;
  --mediumPriority: #eeba0b;
  --lowPriority: #008000;
  --nonePriority: #495057;
}

/* RESET */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* GLOBAL STYLES */
html {
  font-size: 10px;
}

body,
html {
  height: 100%;
  min-height: 100vh;
}

body {
  font-size: 1.6rem;
}

button {
  cursor: pointer;
  border: none;
  background: none;
  outline: none;
  padding: 4px 8px;
  border-radius: 4px;
  font-family: inherit;
}

button:hover {
  cursor: pointer;
  background-color: var(--secondaryHover);
}

.icon {
  width: 2.8rem;
  height: 2.8rem;
}

/* LAYOUT */
#container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  font-family: "poppins", sans-serif;
}

/* SIDEBAR */
.sidebar {
  background-color: var(--secondary);
  color: var(--text);
  width: 20%;
  height: 100%;
  padding: 1.2rem;
  font-family: "poppins", sans-serif;
  position: sticky;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.sidebar-header button.profile {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 2rem;
}

.sidebar-header button.profile img {
  width: 3rem;
  height: 3rem;
}

.top-sidebar-btns {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.top-sidebar-btns button {
  display: flex;
}

.tasks-menu {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.tasks-menu button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  font-size: 1.8rem;
}

.tasks-menu .tasks-header {
  display: flex;
  justify-content: space-between;
  font-size: 1.6rem;
}

.tasks-menu .tasks-container {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.tasks-menu .task-item {
  display: flex;
  justify-content: space-between;
}

.tasks-menu .task-item.active {
  background-color: var(--secondaryHover);
}

.tasks-menu .task-item-left {
  display: flex;
  flex-grow: 1;
  gap: 12px;
}

.task-item .todo-length {
  font-size: 1.4rem;
}

.projects-menu {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.projects-menu button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  font-size: 1.8rem;
  text-transform: capitalize;
}

.projects-menu .projects-header {
  display: flex;
  justify-content: space-between;
  font-size: 1.6rem;
}

.projects-menu .projects-container {
  display: flex;
  flex-direction: column;
  gap: 4px;
  overflow: auto;
  height: 300px;
  /* height: 100%; */
}

.projects-menu .project-item {
  display: flex;
  justify-content: space-between;
}

.projects-menu .project-item.active {
  background-color: var(--secondaryHover);
}

.projects-menu .project-item-left {
  display: flex;
  flex-grow: 1;
  gap: 12px;
}

.project-item .todo-length {
  font-size: 1.4rem;
}

/* TODO CONTAINER */
.todo-container {
  width: 80%;
  height: 100%;
  padding: 4rem 2rem;
  background-color: var(--background);
}

.todo-header {
  display: flex;
  flex-direction: row;
  gap: 0.8rem;
  align-items: baseline;
  justify-content: flex-start;
  margin-bottom: 2rem;
  font-family: "poppins", sans-serif;
  padding-bottom: 1rem;
  border-bottom: 1px solid #000;
  text-transform: capitalize;
  position: sticky;
}

.number-of-todos {
  font-size: 2.4rem;
  background-color: var(--accent);
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.todo-list-container {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  /* height: auto; */
  height: 500px;
  overflow: auto;
}

/* TODO ITEM */
.todo-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 1.6rem;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-family: "poppins", sans-serif;
  transition: all 0.2s;
  gap: 16px;
}

.todo-item.todo-item-checked {
  background-color: #f9f9f9;
}

.todo-item.todo-item-checked:hover {
  background-color: #f9f9f9;
  color: #ccc;
}

.todo-item.todo-item-checked .todo-item-title {
  text-decoration: line-through;
  color: #ccc;
}

.todo-item.todo-item-checked .todo-item-description {
  text-decoration: line-through;
  color: #ccc;
}

.todo-item.todo-item-checked .todo-item-date {
  color: #999;
}

.todo-item.todo-item-checked .todo-item-right :not(.todo-item-date) {
  display: none;
}

.todo-item:hover {
  background-color: var(--secondary);
  color: var(--text);
}

.todo-item button {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 4px;
}

.todo-item-left {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.todo-item-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.todo-item-title {
  font-size: 2rem;
}

.todo-item-title::first-letter {
  text-transform: uppercase;
}

.todo-item-description {
  font-size: 1.6rem;
  color: #333;
  flex: 1;
}

.todo-item-priority {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  margin: 0.8rem;
}

.lowPriority-todo {
  background-color: var(--lowPriority);
}

.mediumPriority-todo {
  background-color: var(--mediumPriority);
}

.highPriority-todo {
  background-color: var(--highPriority);
}

.nonePriority-todo {
  background-color: var(--nonePriority);
}

.lowPriority-text {
  color: var(--lowPriority);
}

.mediumPriority-text {
  color: var(--mediumPriority);
}

.highPriority-text {
  color: var(--highPriority);
}

.nonePriority-text {
  color: var(--nonePriority);
}

.todo-item-date {
  font-size: 1.6rem;
  margin-right: 3.6rem;
}

/* MODALS */
dialog {
  margin: 150px auto auto;
  border-radius: 4px;
  padding: 16px;
  background-color: var(--background);
  border: 2px solid #999;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 600px;
}

dialog input,
dialog select {
  border: none;
  outline: none;
  font-family: inherit;
  color: (var(--text));
  cursor: pointer;
}

dialog input[type="text"] {
  background-color: transparent;
}

dialog button {
  font-size: 1.6rem;
  padding: 4px 12px;
}

dialog .cancel-btn {
  background-color: #999;
}

dialog .cancel-btn:hover {
  background-color: var(--secondaryHover);
}

dialog .confirm-btn {
  margin-left: 8px;
  color: #fff;
  background-color: var(--accent);
}

dialog .confirm-btn:hover {
  color: var(--text);
  background-color: var(--secondaryHover);
}

.add-or-edit-modal .dialog-top {
  display: flex;
  flex-direction: column;
}

.add-or-edit-modal .dialog-top input {
  background-color: transparent;
}

.add-or-edit-modal input#task-title,
.add-or-edit-modal input#project-title {
  color: (var(--text));
  font-size: 2.8rem;
  font-weight: 800;
}

.add-or-edit-modal input#task-title::placeholder {
  font-size: 2.8rem;
  opacity: 50%;
}

.add-or-edit-modal input#task-description {
  font-size: 1.6rem;
  margin-bottom: 16px;
}

.add-or-edit-modal input#task-description::placeholder {
  font-size: 1.6rem;
  opacity: 50%;
}

.add-or-edit-modal input#due-date {
  font-size: 1.6rem;
  background-color: #999;
  border-radius: 2px;
  padding: 4px;
  margin-right: 16px;
}

.add-or-edit-modal select {
  background-color: #999;
  padding: 4px;
  border-radius: 2px;
  font-size: 1.6rem;
}

.add-or-edit-modal .dialog-middle {
  margin-bottom: 20px;
}

.add-or-edit-modal .dialog-bottom {
  padding-top: 20px;
  border-top: 1px solid #999;
  display: flex;
  justify-content: space-between;
}

.add-or-edit-modal input#project-title {
  color: (var(--text));
  font-size: 2.8rem;
  font-weight: 800;
}

.add-or-edit-modal .dialog-top {
  margin-bottom: 20px;
}

.add-or-edit-modal .dialog-bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.add-or-edit-modal input#project-title::placeholder {
  color: (var(--text));
  font-size: 2.8rem;
  opacity: 50%;
}

#delete-todo-modal h3 {
  font-size: 2.4rem;
  margin-bottom: 4px;
}

#delete-todo-modal .warning {
  font-size: 1.6rem;
  margin-bottom: 20px;
}

#delete-todo-modal .dialog-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}

#delete-todo-modal .delete-btn {
  background-color: #d00000;
  color: #fff;
  font-weight: 500;
}

#delete-todo-modal .todo-name {
  font-weight: 700;
  font-size: 2rem;
}
