tns button {\n  display: flex;\n}\n\n.tasks-menu {\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n  margin-bottom: 2rem;\n}\n\n.tasks-menu button {\n  display: flex;\n  justify-content: flex-start;\n  align-items: center;\n  gap: 1rem;\n  font-size: 1.8rem;\n}\n\n.tasks-menu .tasks-header {\n  display: flex;\n  justify-content: space-between;\n  font-size: 1.6rem;\n}\n\n.tasks-menu .tasks-container {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.tasks-menu .task-item {\n  display: flex;\n  justify-content: space-between;\n}\n\n.tasks-menu .task-item.active {\n  background-color: var(--secondaryHover);\n}\n\n.tasks-menu .task-item-left {\n  display: flex;\n  flex-grow: 1;\n  gap: 12px;\n}\n\n.task-item .todo-length {\n  font-size: 1.4rem;\n}\n\n.projects-menu {\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n}\n\n.projects-menu button {\n  display: flex;\n  justify-content: flex-start;\n  align-items: center;\n  gap: 1rem;\n  font-size: 1.8rem;\n  text-transform: capitalize;\n}\n\n.projects-menu .projects-header {\n  display: flex;\n  justify-content: space-between;\n  font-size: 1.6rem;\n}\n\n.projects-menu .projects-container {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n  overflow: auto;\n  height: 300px;\n  /* height: 100%; */\n}\n\n.projects-menu .project-item {\n  display: flex;\n  justify-content: space-between;\n}\n\n.projects-menu .project-item.active {\n  background-color: var(--secondaryHover);\n}\n\n.projects-menu .project-item-left {\n  display: flex;\n  flex-grow: 1;\n  gap: 12px;\n}\n\n.project-item .todo-length {\n  font-size: 1.4rem;\n}\n\n/* TODO CONTAINER */\n.todo-container {\n  width: 80%;\n  height: 100%;\n  padding: 4rem 2rem;\n  background-color: var(--background);\n}\n\n.todo-header {\n  display: flex;\n  flex-direction: row;\n  gap: 0.8rem;\n  align-items: baseline;\n  justify-content: flex-start;\n  margin-bottom: 2rem;\n  font-family: \"poppins\", sans-serif;\n  padding-bottom: 1rem;\n  border-bottom: 1px solid #000;\n  text-transform: capitalize;\n  position: sticky;\n}\n\n.number-of-todos {\n  font-size: 2.4rem;\n  background-color: var(--accent);\n  width: 3.2rem;\n  height: 3.2rem;\n  border-radius: 50%;\n  font-weight: 600;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  color: white;\n}\n\n.todo-list-container {\n  display: flex;\n  flex-direction: column;\n  gap: 1.6rem;\n  /* height: auto; */\n  height: 500px;\n  overflow: auto;\n}\n\n/* TODO ITEM */\n.todo-item {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  align-items: center;\n  padding: 0.8rem 1.6rem;\n  background-color: #fff;\n  border-radius: 8px;\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n  cursor: pointer;\n  font-family: \"poppins\", sans-serif;\n  transition: all 0.2s;\n  gap: 16px;\n}\n\n.todo-item.todo-item-checked {\n  background-color: #f9f9f9;\n}\n\n.todo-item.todo-item-checked:hover {\n  background-color: #f9f9f9;\n  color: #ccc;\n}\n\n.todo-item.todo-item-checked .todo-item-title {\n  text-decoration: line-through;\n  color: #ccc;\n}\n\n.todo-item.todo-item-checked .todo-item-description {\n  text-decoration: line-through;\n  color: #ccc;\n}\n\n.todo-item.todo-item-checked .todo-item-date {\n  color: #999;\n}\n\n.todo-item.todo-item-checked .todo-item-right :not(.todo-item-date) {\n  display: none;\n}\n\n.todo-item:hover {\n  background-color: var(--secondary);\n  color: var(--text);\n}\n\n.todo-item button {\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n  padding: 4px;\n}\n\n.todo-item-left {\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n}\n\n.todo-item-right {\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n}\n\n.todo-item-title {\n  font-size: 2rem;\n}\n\n.todo-item-title::first-letter {\n  text-transform: uppercase;\n}\n\n.todo-item-description {\n  font-size: 1.6rem;\n  color: #333;\n  flex: 1;\n}\n\n.todo-item-priority {\n  width: 2rem;\n  height: 2rem;\n  border-radius: 50%;\n  margin: 0.8rem;\n}\n\n.lowPriority-todo {\n  background-color: var(--lowPriority);\n}\n\n.mediumPriority-todo {\n  background-color: var(--mediumPriority);\n}\n\n.highPriority-todo {\n  background-color: var(--highPriority);\n}\n\n.nonePriority-todo {\n  background-color: var(--nonePriority);\n}\n\n.lowPriority-text {\n  color: var(--lowPriority);\n}\n\n.mediumPriority-text {\n  color: var(--mediumPriority);\n}\n\n.highPriority-text {\n  color: var(--highPriority);\n}\n\n.nonePriority-text {\n  color: var(--nonePriority);\n}\n\n.todo-item-date {\n  font-size: 1.6rem;\n  margin-right: 3.6rem;\n}\n\n/* MODALS */\ndialog {\n  margin: 150px auto auto;\n  border-radius: 4px;\n  padding: 16px;\n  background-color: var(--background);\n  border: 2px solid #999;\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n  width: 600px;\n}\n\ndialog input,\ndialog select {\n  border: none;\n  outline: none;\n  font-family: inherit;\n  color: (var(--text));\n  cursor: pointer;\n}\n\ndialog input[type=\"text\"] {\n  background-color: transparent;\n}\n\ndialog button {\n  font-size: 1.6rem;\n  padding: 4px 12px;\n}\n\ndialog .cancel-btn {\n  background-color: #999;\n}\n\ndialog .cancel-btn:hover {\n  background-color: var(--secondaryHover);\n}\n\ndialog .confirm-btn {\n  margin-left: 8px;\n  color: #fff;\n  background-color: var(--accent);\n}\n\ndialog .confirm-btn:hover {\n  color: var(--text);\n  background-color: var(--secondaryHover);\n}\n\n.add-or-edit-modal .dialog-top {\n  display: flex;\n  flex-direction: column;\n}\n\n.add-or-edit-modal .dialog-top input {\n  background-color: transparent;\n}\n\n.add-or-edit-modal input#task-title,\n.add-or-edit-modal input#project-title {\n  color: (var(--text));\n  font-size: 2.8rem;\n  font-weight: 800;\n}\n\n.add-or-edit-modal input#task-title::placeholder {\n  font-size: 2.8rem;\n  opacity: 50%;\n}\n\n.add-or-edit-modal input#task-description {\n  font-size: 1.6rem;\n  margin-bottom: 16px;\n}\n\n.add-or-edit-modal input#task-description::placeholder {\n  font-size: 1.6rem;\n  opacity: 50%;\n}\n\n.add-or-edit-modal input#due-date {\n  font-size: 1.6rem;\n  background-color: #999;\n  border-radius: 2px;\n  padding: 4px;\n  margin-right: 16px;\n}\n\n.add-or-edit-modal select {\n  background-color: #999;\n  padding: 4px;\n  border-radius: 2px;\n  font-size: 1.6rem;\n}\n\n.add-or-edit-modal .dialog-middle {\n  margin-bottom: 20px;\n}\n\n.add-or-edit-modal .dialog-bottom {\n  padding-top: 20px;\n  border-top: 1px solid #999;\n  display: flex;\n  justify-content: space-between;\n}\n\n.add-or-edit-modal input#project-title {\n  color: (var(--text));\n  font-size: 2.8rem;\n  font-weight: 800;\n}\n\n.add-or-edit-modal .dialog-top {\n  margin-bottom: 20px;\n}\n\n.add-or-edit-modal .dialog-bottom {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n}\n\n.add-or-edit-modal input#project-title::placeholder {\n  color: (var(--text));\n  font-size: 2.8rem;\n  opacity: 50%;\n}\n\n#delete-todo-modal h3 {\n  font-size: 2.4rem;\n  margin-bottom: 4px;\n}\n\n#delete-todo-modal .warning {\n  font-size: 1.6rem;\n  margin-bottom: 20px;\n}\n\n#delete-todo-modal .dialog-actions {\n  display: flex;\n  justify-content: flex-end;\n  gap: 8px;\n}\n\n#delete-todo-modal .delete-btn {\n  background-color: #d00000;\n  color: #fff;\n  font-weight: 500;\n}\n\n#delete-todo-modal .todo-name {\n  font-weight: 700;\n  font-size: 2rem;\n}\n"],"sourceRoot":""}]);
